import React from 'react'
import Link from 'gatsby-link'
import BurgerMenu from 'react-burger-menu';
import { slide as Menu } from 'react-burger-menu'

class Example extends React.Component {
    showSettings (event) {
        event.preventDefault();

    }

    render () {
        return (
            <Menu right burgerButtonClassName={ "black-border-all" } >
                <a className="menu-item turquoise-text text-uppercase center-align" href="/"><img src="../static/PMP-logo-mobile.svg" alt="" height={52}/></a>
                <a className="turquoise-text-1 text-uppercase bold-text center-align" href="http://purposemeetsprofit.co/giveaway/">Giveaway</a>
                <a className="center-align" href="https://www.youtube.com/channel/UColDe80cdpOMHn3d7ecJxVg" target="_blank"><i className="fa fa-youtube-play turquoise-text-1" aria-hidden="true"/></a>
                <a className="center-align" href="https://twitter.com/Fownders?ref_src=twsrc%5Egoogle%7Ctwcamp%5Eserp%7Ctwgr%5Eauthor" target="_blank"><i className="fa fa-twitter turquoise-text-1" aria-hidden="true"/></a>
                <a className="center-align" href="https://www.facebook.com/fowndersnewark/" target="_blank"><i className="fa fa-facebook-square turquoise-text-1" aria-hidden="true"/></a>
                <a className="center-align" href="https://www.instagram.com/purposemeetsprofit/" target="_blank"><i className="fa fa-instagram turquoise-text-1" aria-hidden="true"/></a>
                {/*<a id="about" className="" href="/about">About</a>*/}
                {/*<a id="contact" className="menu-item" href="/contact">Contact</a>*/}
                {/*<a onClick={ this.showSettings } className="menu-item--small" href="">Settings</a>*/}
            </Menu>
        );
    }
}

export default Example;


